import { createAction, createEmptyAction } from 'tools/create-action';

import {
    ShowItemsCommandPayload,
    AddItemCommandPayload,
    RemoveItemCommandPayload,
    RemoveAllCommandPayload,
    GoToCatalogCommandPayload,
    GoToWaitCommandPayload,
    ValidationOkCommandPayload,
    GoBackCommandPayload,
    AddressInputPayload,
    AddressInputAddPayload,
    AddressInputClearPayload,
    AddressFullInputAddPayload,
    AddressFullInputClearPayload,
    AddressFullValidationOkPayload,
    AddressFullValidationErrorPayload,
    AskAgainForOrderStatusPayload,
    AskAgainForOrderStatusFinalPayload,
    AddressFullInputCommandPayload,
    OrderErrorPayload,
} from '../constants/native';

export const showItemsAction = (payload: ShowItemsCommandPayload) => createAction('SHOW_ITEMS', payload);

export const addItemAction = (payload: AddItemCommandPayload) => createAction('ADD_ITEM', payload);

interface RemoveItemCommandPayloadExtended extends RemoveItemCommandPayload {
    shouldRemoveMention?: boolean;
}
export const removeItemAction = (payload: RemoveItemCommandPayloadExtended) => createAction('REMOVE_ITEM', payload);

export const removeAllAction = (payload: RemoveAllCommandPayload) => createEmptyAction('REMOVE_ALL');

export const goToCatalogAction = (payload: GoToCatalogCommandPayload) => createAction('GO_TO_CATALOG', payload);

export const goBackAction = (payload: GoBackCommandPayload) => createEmptyAction('GO_BACK');

export const goToWaitAction = (payload: GoToWaitCommandPayload) => createEmptyAction('GO_TO_WAIT');

export const validationOkAction = (payload: ValidationOkCommandPayload) => createAction('VALIDATION_OK', payload);

export const orderErrorAction = (payload: OrderErrorPayload) => createAction('ORDER_ERROR', payload);

export const addressInputAction = (payload: AddressInputPayload) => createAction('ADDRESS_INPUT', payload);

export const addressInputAddAction = (payload: AddressInputAddPayload) => createAction('ADDRESS_INPUT_ADD', payload);

export const addressInputClearAction = (payload: AddressInputClearPayload) => createEmptyAction('ADDRESS_INPUT_CLEAR');

export const addressFullInputAction = (payload: AddressFullInputCommandPayload) =>
    createAction('ADDRESS_FULL_INPUT', payload);

export const addressFullInputAddAction = (payload: AddressFullInputAddPayload) =>
    createAction('ADDRESS_FULL_INPUT_ADD', payload);

export const addressFullInputClearAction = (payload: AddressFullInputClearPayload) =>
    createEmptyAction('ADDRESS_FULL_INPUT_CLEAR');

export const addressFullValidationErrorAction = (payload: AddressFullValidationErrorPayload) =>
    createEmptyAction('ADDRESS_FULL_VALIDATION_ERROR');

export const addressFullSuccessAction = () => createEmptyAction('ADDRESS_FULL_SUCCESS');

export const askAgainForOrderStatusAction = (payload: AskAgainForOrderStatusPayload) =>
    createEmptyAction('ASK_AGAIN_FOR_ORDER_STATUS');

export const askAgainForOrderStatusFinalAction = (payload: AskAgainForOrderStatusFinalPayload) =>
    createEmptyAction('ASK_AGAIN_FOR_ORDER_STATUS_FINAL');
